.container {
  position: absolute;
  z-index:  1;
}

.row {
  height:        106.25px;
  margin-bottom: 15px;
  &:last-child {
    margin-bottom: 0;
  }
}

.cell {
  float:            left;
  width:            106.25px;
  height:           106.25px;
  background-color: hsla(0, 0%, 100%, 0.2);
  border-radius:    3px;
  margin-right:     15px;
  &:last-child {
    margin-right: 0;
  }
}



